import React, { Component } from "react";
import bitmoji from "../../assets/img/icon.png";

class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      active: null
    };
  }

  componentWillMount() {
    this.props.setChangeActive(this._changeActive);
  }

  _changeActive = active => this.setState({ active });

  render() {
    return (
      <header>
        <nav className="navbar navbar-expand-lg navbar-light bg-light fixed-top">
          <div className="container">
            <button
              className="navbar-toggler"
              type="button"
              data-toggle="collapse"
              data-target="#navbar"
            >
              <span className="navbar-toggler-icon" />
            </button>
            <div
              className="collapse navbar-collapse justify-content-md-center"
              id="navbar"
            >
              <ul className="navbar-nav">
                <li
                  className={`nav-item ${
                    this.state.active === "About" ? "active" : ""
                  }`}
                >
                  <a className="nav-link" href="#about">
                    <span>About me</span>
                  </a>
                </li>
                <li
                  className={`nav-item ${
                    this.state.active === "Thoughts" ? "active" : ""
                  }`}
                >
                  <a className="nav-link" href="#thoughts">
                    <span>Thoughts</span>
                  </a>
                </li>
                <li className="nav-item">
                  <a className="nav-link" href="#quote">
                    <img
                      alt="El Mehdi ASSALI Bitmoji"
                      src={bitmoji}
                      height="48"
                    />
                  </a>
                </li>
                <li
                  className={`nav-item ${
                    this.state.active === "Projects" ? "active" : ""
                  }`}
                >
                  <a className="nav-link" href="#projects">
                    <span>Projects</span>
                  </a>
                </li>
                <li
                  className={`nav-item ${
                    this.state.active === "Skills" ? "active" : ""
                  }`}
                >
                  <a className="nav-link" href="#skills">
                    <span>Skills</span>
                  </a>
                </li>
              </ul>
            </div>
          </div>
        </nav>
      </header>
    );
  }
}

export default Header;
