import React, { Component, Fragment } from "react";
import Header from "../Header";
import Footer from "../Footer";
import Projects from "../Projects";
import About from "../About";
import Skills from "../Skills";
import Thoughts from "../Thoughts";
import Quote from "../Quote";

class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {
      changeActive: null
    };
  }

  componentDidMount() {
    document.title = `El Mehdi ASSALI - Portfolio`;
  }

  _setChangeActive = changeActive => this.setState({ changeActive });

  render() {
    return (
      <Fragment>
        <Header setChangeActive={this._setChangeActive} />
        <main id="main">
          <div className="container-fluid">
            <Quote after changeActive={this.state.changeActive} />
            <About gray after before changeActive={this.state.changeActive} />
            <Skills after before changeActive={this.state.changeActive} />
            <Projects
              gray
              after
              before
              changeActive={this.state.changeActive}
            />
            <Thoughts before changeActive={this.state.changeActive} />
          </div>
        </main>
        <Footer />
      </Fragment>
    );
  }
}

export default Home;
