import ReactDOM from "react-dom";
import React, { Component } from "react";

class Quote extends Component {
  componentDidMount() {
    window.addEventListener("scroll", this.handleScroll);
  }

  componentWillUnmount() {
    window.removeEventListener("scroll", this.handleScroll);
  }

  handleScroll = event => {
    const rect = ReactDOM.findDOMNode(this).getBoundingClientRect();
    const { height, top, bottom } = rect;
    const windowHeight = window.innerHeight;
    const pxVisible = Math.max(
      0,
      top > 0
        ? Math.min(height, windowHeight - top)
        : Math.min(bottom, windowHeight)
    );
    const inSection = pxVisible / windowHeight >= 0.5;
    if (inSection) {
      this.props.changeActive(null);
    }
  };

  render() {
    return <div id="quote" className="section section-separator-after p-0" />;
  }
}

export default Quote;
