import ReactDOM from "react-dom";
import React, { Component } from "react";
import shortId from "shortid";
import projects from "./projects";

class Projects extends Component {
  componentDidMount() {
    window.addEventListener("scroll", this.handleScroll);
  }

  componentWillUnmount() {
    window.removeEventListener("scroll", this.handleScroll);
  }

  handleScroll = event => {
    const rect = ReactDOM.findDOMNode(this).getBoundingClientRect();
    const { height, top, bottom } = rect;
    const windowHeight = window.innerHeight;
    const pxVisible = Math.max(
      0,
      top > 0
        ? Math.min(height, windowHeight - top)
        : Math.min(bottom, windowHeight)
    );
    const inSection = pxVisible / windowHeight >= 0.5;
    if (inSection) {
      this.props.changeActive("Projects");
    }
  };

  _renderProject = ({ name, technologies, description, repository }) => (
    <div className="card sharp-corner" key={shortId.generate()}>
      <div className="card-body">
        <h3 className="card-title mb-3">{name}</h3>
        <div className="card-subtitle text-muted">
          {technologies.join(" | ")}
        </div>
        <hr />
        <div className="card-text">{description}</div>
        <hr />
        {repository && (
          <div className="card-text text-muted">
            You can see{" "}
            <a
              className="card-link"
              href={repository}
              target="_blank"
              rel="noopener noreferrer"
            >
              source code.
            </a>
          </div>
        )}
      </div>
    </div>
  );

  _renderCategoryProjects = ({ category, projects }, idx) => (
    <div
      className={`tab-pane fade ${idx === 0 ? "show active" : ""}`}
      id={`${category.toLowerCase().replace(/( )*/g, "")}-projects`}
      role="tabpanel"
      key={shortId.generate()}
    >
      <div className="card-columns">{projects.map(this._renderProject)}</div>
    </div>
  );

  _renderCategoryTab = ({ category }, idx) => (
    <a
      className={`nav-item nav-link ${idx === 0 ? "active" : ""}`}
      data-toggle="tab"
      href={`#${category.toLowerCase().replace(/( )*/g, "")}-projects`}
      role="tab"
      key={shortId.generate()}
    >
      <span>{category}</span>
    </a>
  );

  render() {
    const { gray, after, before } = this.props;
    return (
      <div
        id="projects"
        className={`section ${gray ? "section-gray" : ""} ${
          before ? "section-separator-before" : ""
        } ${after ? "section-separator-after" : ""}`}
      >
        <h1 className="section-title">Projects</h1>
        <hr className="section-title-underline" />
        <div className="section-body container">
          <nav className="d-none d-md-block">
            <div className="nav nav-tabs" id="projects-nav" role="tablist">
              {projects.map(this._renderCategoryTab)}
            </div>
          </nav>
          <div className="tab-content" id="projects-body">
            {projects.map(this._renderCategoryProjects)}
          </div>
        </div>
      </div>
    );
  }
}

export default Projects;
