import ReactDOM from "react-dom";
import React, { Component } from "react";
import skills from "./skills";
import shortId from "shortid";

class Skills extends Component {
  componentDidMount() {
    window.addEventListener("scroll", this._handleScroll);
  }

  componentWillUnmount() {
    window.removeEventListener("scroll", this._handleScroll);
  }

  _handleScroll = () => {
    const rect = ReactDOM.findDOMNode(this).getBoundingClientRect();
    const { height, top, bottom } = rect;
    const windowHeight = window.innerHeight;
    const pxVisible = Math.max(
      0,
      top > 0
        ? Math.min(height, windowHeight - top)
        : Math.min(bottom, windowHeight)
    );
    const inSection = pxVisible / windowHeight >= 0.5;
    if (inSection) {
      this.props.changeActive("Skills");
    }
  };

  _renderSkill = ({ name, icon, iconImg }) => (
    <div className="card sharp-corner" key={shortId.generate()}>
      <div className="card-body">
        <h3 className="card-title m-0">
          {icon && <i className={`skill-logo ${icon}`} />}
          {iconImg && (
            <img src={iconImg} alt="Skill logo" className={`skill-img`} />
          )}
          {name}
        </h3>
      </div>
    </div>
  );

  _renderCategorySkills = ({ category, skills }, idx) => (
    <div
      className={`tab-pane fade ${idx === 0 ? "show active" : ""}`}
      id={`${category.replace(/[ ]/g, "-").toLowerCase()}-skills`}
      role="tabpanel"
      key={shortId.generate()}
    >
      <div className="card-columns">{skills.map(this._renderSkill)}</div>
    </div>
  );

  _renderCategoryTab = ({ category }, idx) => (
    <a
      className={`nav-item nav-link ${idx === 0 ? "active" : ""}`}
      data-toggle="tab"
      href={`#${category.replace(/[ ]/g, "-").toLowerCase()}-skills`}
      role="tab"
      key={shortId.generate()}
    >
      <span>{category}</span>
    </a>
  );

  render() {
    const { gray, after, before } = this.props;
    return (
      <div
        id="skills"
        className={`section ${gray ? "section-gray" : ""} ${
          before ? "section-separator-before" : ""
        } ${after ? "section-separator-after" : ""}`}
      >
        <h1 className="section-title">Skills</h1>
        <hr className="section-title-underline" />
        <div className="section-body container">
          <nav className="d-none d-md-block">
            <div className="nav nav-tabs" id="skills-nav" role="tablist">
              {skills.map(this._renderCategoryTab)}
            </div>
          </nav>
          <div className="tab-content" id="skills-body">
            {skills.map(this._renderCategorySkills)}
          </div>
        </div>
      </div>
    );
  }
}

export default Skills;
