import React, {Component, Fragment} from "react";
import { Link } from 'react-router-dom';
import Footer from "../Footer";
import thoughts from './thoughts';
import Spinner from '../Spinner/index';

class ThoughtRead extends Component {
  constructor(props) {
    super(props);
    this.state = {
      thought: null
    };
  }

  componentWillMount() {
    const thought = thoughts.find(e => e.link === this.props.match.params.thoughtLink);
    this.setState({thought});
  }

  render() {
    return (
      (this.state.thought &&
      <Fragment>
        <header id="article-read-header">
          <nav className="navbar navbar-expand navbar-light bg-light">
            <div className="container">
              <button
                className="navbar-toggler"
                type="button"
                data-toggle="collapse"
                data-target="#navbar"
              >
                <span className="navbar-toggler-icon" />
              </button>
              <div
                className="collapse navbar-collapse justify-content-md-center"
                id="navbar"
              >
                <ul className="navbar-nav">
                  <li className="nav-item nav-item-left">
                    <Link className="nav-link" to="/portfolio/">
                      <span>
                      <i className="fas fa-arrow-left"></i> 
                      </span>
                    </Link>
                  </li>
                  <li className="nav-item nav-item-right d-none d-sm-block">
                    <a className="nav-link">
                      {this.state.thought.name}
                    </a>
                  </li>
                </ul>
              </div>
            </div>
          </nav>
        </header>
        <main id="main" className="mt-5 mb-5">
          <div className="container" id="article-read-body">
            <h5 className="text-center d-block d-sm-none">{this.state.thought.name}</h5>
            <div className="description">
              Ceci est un guide illustrant les différentes étapes necessaires pour pourvoir bénificier d'un programme d'echange (mobilité ou double diplomation) 
              à l'ENSIAS.
              <br />
              <strong>NB:</strong> Puisque ce guide est un retour d'experience, il va y avoir des sections bien spécifiques au programme de double diplomation ENSIAS - ENSIMAG.
            </div>
            <div className="summary">
              <ol>
                <li>
                  Programme d'échange à l'ENSIAS
                  <ol>
                    <li>Types de mobilité</li>
                    <li>Liste des mobilités (2018-2019)</li>
                    <li>Choisir sa mobilité</li>
                    <li>Cas ENSIMAG (2018-2019)</li>
                  </ol>
                </li>
                <li></li>
              </ol>
            </div>
          </div>
        </main>
        <Footer/>
      </Fragment>) || <Spinner/>
    );
  }
}

export default ThoughtRead;