import ReactDOM from "react-dom";
import React, { Component } from "react";
import { Link } from "react-router-dom";
import thoughts from './thoughts';

class Thoughts extends Component {
  componentDidMount() {
    window.addEventListener("scroll", this.handleScroll);
  }

  componentWillUnmount() {
    window.removeEventListener("scroll", this.handleScroll);
  }

  handleScroll = event => {
    const rect = ReactDOM.findDOMNode(this).getBoundingClientRect();
    const { height, top, bottom } = rect;
    const windowHeight = window.innerHeight;
    const pxVisible = Math.max(
      0,
      top > 0
        ? Math.min(height, windowHeight - top)
        : Math.min(bottom, windowHeight)
    );
    const inSection = pxVisible / windowHeight >= 0.5;
    if (inSection) {
      this.props.changeActive("Thoughts");
    }
  };

  _renderThought = ({ name, duration, date, link, lang }, idx) => (
    <Link className="card sharp-corner post mt-3 mb-3" to={`/portfolio/thoughts/${link}`} key={`thought#${idx}`}>
      <div className="card-body">
         <div className="row">
          <div className="col-7 post-name">
            <span className={`flag-icon flag-icon-${lang} mr-3`}></span>
            {name}
            <span className="post-duration">{duration} min read</span>
          </div>
          <div className="col-5 post-date text-right">{date}</div>
        </div>
      </div>
    </Link>
  );

  render() {
    const { gray, after, before } = this.props;
    return (
      <div
        id="thoughts"
        className={`section ${gray ? "section-gray" : ""} ${
          before ? "section-separator-before" : ""
        } ${after ? "section-separator-after" : ""}`}
      >
        <h1 className="section-title">Thoughts</h1>
        <hr className="section-title-underline" />
        <div className="section-body container">
          {thoughts.map(this._renderThought)}
        </div>
      </div>
    );
  }
}

export default Thoughts;
