const projects = [{
    name: "Portfolio",
    technologies: ["React", "Bootstrap 4"],
    description: "A website containing my personal and professional information. " +
      "I constantly publish my thoughts about computer science and new technologies.",
    categories: ["React"]
  },
  {
    name: "md2html",
    technologies: ["Spring Boot", "Javascript"],
    description: "A compiler for the Markdown language. The output of the compiler is HTML styled with CSS and Bootstrap.",
    categories: ["Spring Boot", "Compiler design"],
    repository: "https://github.com/assalielmehdi/md2html"
  },
  {
    name: "Strategic Planning Platform",
    technologies: ["Spring Boot", "React"],
    categories: ["React", "Spring Boot"],
    description: "As a year-end project, I had to make a digital platform for strategic planning. " +
      "The application is made using Spring Boot (API) and React.js (front-end).",
    repository: "https://github.com/assalielmehdi/pfa-front"
  },
  {
    name: "Ada compiler",
    technologies: ["C", "Flex"],
    description: "Complete compiler/interpreter for a subset of the Ada programming language, made for pedagogical purposes.",
    categories: ["Compiler design"],
    repository: "https://github.com/assalielmehdi/sample-spark-ada-compiler"
  }
];

const categories = [
  ...new Set(
    projects
    .map(project => [...project.categories])
    .reduce((cumul, curr) => [...cumul, ...curr], [])
  )
];

const projectsByCategory = [{
    category: "All",
    projects
  },
  ...categories.map(category => ({
    category,
    projects: projects.filter(project => project.categories.includes(category))
  }))
];

export default projectsByCategory;