import ReactDOM from "react-dom";
import React, {
  Component
} from "react";
import resume from "../../assets/docs/resume.pdf";

class About extends Component {
  componentDidMount() {
    window.addEventListener("scroll", this.handleScroll);
  }

  componentWillUnmount() {
    window.removeEventListener("scroll", this.handleScroll);
  }

  handleScroll = event => {
    const rect = ReactDOM.findDOMNode(this).getBoundingClientRect();
    const {
      height,
      top,
      bottom
    } = rect;
    const windowHeight = window.innerHeight;
    const pxVisible = Math.max(
      0,
      top > 0 ?
      Math.min(height, windowHeight - top) :
      Math.min(bottom, windowHeight)
    );
    const inSection = pxVisible / windowHeight >= 0.5;
    if (inSection) {
      this.props.changeActive("About");
    }
  };

  render() {
    const {
      gray,
      after,
      before
    } = this.props;
    return ( <div id = "about"
      className={
        `section ${gray ? "section-gray" : ""} ${
          before ? "section-separator-before" : ""
        } ${after ? "section-separator-after" : ""}`
      }>
        <h1 className="section-title">About me</h1> 
        <hr className="section-title-underline"  />
        <div className="section-body container">
          <div className="text-center">
            <a href={resume} className="link" target="_blank">Download my resume</a>
          </div>
        </div> 
      </div>
    );
  }
}

export default About;